import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import {
  AuthenticationService,
  SkuService,
  SparesGroup,
  ToastService
} from '../../services';
import { debounceTime, filter } from 'rxjs/operators';

@Component({
  selector: 'app-stock-list',
  templateUrl: './stock-list.component.html',
  styleUrls: ['./stock-list.component.scss']
})
export class StockListComponent implements OnInit {
  public isLoading: Boolean = true;
  public searchForm: FormGroup;
  public allProductClassesOption = '';
  public sparesGroups: Array<SparesGroup> = [];
  public getSparesGroupSubscription: Subscription;
  public siteId: string;

  constructor(
    public skuService: SkuService,
    private formBuilder: FormBuilder,
    private toastService: ToastService,
    private authenticationService: AuthenticationService
  ) {
    this.siteId = this.authenticationService.currentUserProfile$.value.primarySite.id;
    this.searchForm = this.formBuilder.group({
      text: ''
    });
  }

  public ngOnInit() {
    this.getSparesGroupSubscription = this.skuService
      .getSparesGroups(undefined, this.siteId)
      .subscribe(
        (data: Array<SparesGroup>) => {
          this.sparesGroups = data;
          this.isLoading = false;
        },
        (err: HttpErrorResponse) => {
          this.toastService.showErrorToast(
            `Sorry something went wrong... please try again later, ${err}`
          );
        }
      );

    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 2 || textValue.length === 0)
      )
      .subscribe(() => this.submitSearch());
  }

  public get noSparesGroups(): Boolean {
    return this.sparesGroups.length === 0;
  }

  public submitSearch() {
    this.getSparesGroupSubscription.unsubscribe();
    this.isLoading = true;
    this.getSparesGroupSubscription = this.skuService
      .getSparesGroups(this.searchForm.get('text').value, this.siteId)
      .subscribe(
        (data: Array<SparesGroup>) => {
          this.sparesGroups = data;
          this.isLoading = false;
        },
        (err: HttpErrorResponse) => {
          this.toastService.showErrorToast(
            `Sorry something went wrong... please try again later, ${err}`
          );
        }
      );
  }
}
