import {
  Component,
  HostBinding,
  Input,
  OnInit,
  OnDestroy
} from '@angular/core';
import { BasketItem } from 'src/app/services/basket/basket.types';
import {
  ISkuStock,
  AuthenticationService,
  PermissionGroup,
  BasketService,
  SparesPermissions,
  SkuService,
  ToastService
} from 'src/app/services';
import { Subscription } from 'rxjs';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { take } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
import { safelyUnsubscribeFrom } from 'src/utils/observableUtils';

@Component({
  selector: 'app-basket-item',
  templateUrl: './basket-item.component.html',
  styleUrls: ['./basket-item.component.scss']
})
export class BasketItemComponent implements OnInit, OnDestroy {
  @Input()
  public basketItem: BasketItem;
  @HostBinding('class.action-in-progress')
  public itemActionInProgress = false;
  public quantityChangeModalVisible = false;
  public qtyForm: FormGroup;
  public canModifyBasket: boolean;
  public skuStock: ISkuStock;
  public isGettingSkuInfo = false;
  public isUpdatingQty = false;
  public skuInfoSubscription: Subscription;
  public isCurrentUserManager = false;
  public isRequestedUser = false;
  public currentUserId: string;

  constructor(
    private basketService: BasketService,
    private authenticationService: AuthenticationService,
    private formBuilder: FormBuilder,
    private skuService: SkuService,
    private toastService: ToastService
  ) {}

  public ngOnInit() {
    this.setPermissionBooleans();
  }

  public ngOnDestroy() {
    safelyUnsubscribeFrom(this.skuInfoSubscription);
  }

  private setPermissionBooleans() {
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.Spares]: [SparesPermissions.CanModifyBasket]
      })
      .subscribe(hasPermission => (this.canModifyBasket = hasPermission));

    this.authenticationService
      .currentUserIsManager()
      .subscribe(isManager => (this.isCurrentUserManager = isManager));

    this.currentUserId = this.authenticationService.currentUserProfile$.getValue().userId;

    this.isRequestedUser =
      this.currentUserId === this.basketItem.createdByUserId;
  }

  public handleQtyClipboard(event: ClipboardEvent) {
    event.preventDefault();
    return;
  }

  public disableRightClick(e) {
    e.preventDefault();
  }

  public handleDeleteItem() {
    if (this.isCurrentUserManager || this.isRequestedUser) {
      this.itemActionInProgress = true;
      this.basketService
        .deleteBasketItem(
          this.basketItem.skuId,
          this.basketItem.createdByUserId,
          this.currentUserId
        )
        .pipe(take(1))
        .subscribe(x => x);
    } else {
      this.toastService.showErrorToast(
        `You do not have permission to delete this basket item!`
      );
    }
  }

  public handleQtyChangeBtnClick() {
    if (this.isCurrentUserManager || this.isRequestedUser) {
      this.isGettingSkuInfo = true;
      this.qtyForm = this.formBuilder.group({
        qty: [
          this.basketItem.qty,
          [Validators.min(1), Validators.max(999999), Validators.required]
        ]
      });
      this.skuInfoSubscription = this.skuService
        .getSkuStockForSkuId(this.basketItem.skuId)
        .subscribe(
          (sku: ISkuStock[]) => {
            this.isGettingSkuInfo = false;
            if (sku.length > 0) {
              this.skuStock = sku[0];
              this.quantityChangeModalVisible = true;
            } else {
              const errorMessage =
                'Sorry something went wrong, please try again later';
              this.toastService.showErrorToast(errorMessage);
            }
          },
          _ => {
            const errorMessage =
              'Sorry something went wrong, please try again later';
            this.toastService.showErrorToast(errorMessage);
            this.isGettingSkuInfo = false;
          }
        );
    } else {
      this.toastService.showErrorToast(
        `You do not have permission to update this basket item!`
      );
      this.isGettingSkuInfo = false;
    }
  }

  public handleUpdateQty() {
    if (this.isUpdatingQty) {
      return;
    }
    this.isUpdatingQty = true;
    this.basketService
      .updateBasketItem(this.basketItem.skuId, this.qtyForm.value.qty)
      .subscribe(
        _ => {
          this.isUpdatingQty = false;
          this.quantityChangeModalVisible = false;
        },
        (errorResponse: HttpErrorResponse) => {
          this.isUpdatingQty = false;
          const errorMessage = errorResponse.error;
          this.toastService.showErrorToast(errorMessage);
          return;
        }
      );
  }

  public handleQtyKeyup(event: KeyboardEvent) {
    if (
      event.key === 'Enter' &&
      this.qtyForm.value.qty > 0 &&
      this.qtyForm.value.qty.toString().length > 0 &&
      this.qtyForm.value.qty >= this.skuStock.quantities.minimumOrderQuantity
    ) {
      event.preventDefault();
      this.handleUpdateQty();
    }
  }

  public handleQtyInputKeyDown(event) {
    const qtyStringExceedsMaxLength =
      this.qtyForm.value.qty.toString().length >= 6;
    if (
      this.qtyForm.valid &&
      this.qtyForm.value.qty > 0 &&
      this.qtyForm.value.qty >= this.skuStock.quantities.minimumOrderQuantity &&
      event.key === 'Enter'
    ) {
      return;
    }
    if (event.key === 'Backspace' || event.key === 'Delete') {
      return;
    }
    if (qtyStringExceedsMaxLength || isNaN(event.key) || event.keyCode === 32) {
      event.preventDefault();
    }
  }
}
