import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { ISkuStock, SkuService, SparesGroup } from '../../../services';

@Component({
  selector: 'app-spares-group',
  templateUrl: './spares-group.component.html',
  styleUrls: ['./spares-group.component.scss']
})
export class SparesGroupComponent implements OnInit {
  @Input()
  public sparesGroup: SparesGroup;
  @Input()
  public textFilter: string;
  @Input()
  public siteId: string;
  public skuStocks: ISkuStock[] = [];
  public visuallyExpanded = false;
  public offset = 0;
  private takeSize = 10;
  public shouldGetNextSkuStocks = true;
  public isGettingNextSkuStocks = false;
  public maxHeight: string;

  constructor(public skuService: SkuService, public elementRef: ElementRef) {}

  public ngOnInit() {}

  public handleRowToggleClick() {
    if (this.skuStocks.length === 0 && !this.isGettingNextSkuStocks) {
      this.getNextSkuStocks();
    }
    this.visuallyExpanded = !this.visuallyExpanded;
    this.setMaxHeight();
  }

  public getNextSkuStocks() {
    this.isGettingNextSkuStocks = true;
    this.skuService
      .getSkuStockForSparesGroup(
        this.sparesGroup.groupId,
        this.sparesGroup.subGroupId,
        this.takeSize,
        this.offset,
        this.siteId,
        this.textFilter
      )
      .subscribe((skus: ISkuStock[]) => {
        this.skuStocks.push(...skus);
        this.offset += this.takeSize;
        if (skus.length < this.takeSize) {
          this.shouldGetNextSkuStocks = false;
        }
        this.isGettingNextSkuStocks = false;
        this.handleSkuStockHeightChanged();
      });
  }

  public handleSkuStockHeightChanged(): void {
    setTimeout(() => this.setMaxHeight(), 200);
  }

  public setMaxHeight() {
    const children = this.elementRef.nativeElement.children[1].children;
    let totalHeight = 0;

    for (const child of children) {
      totalHeight += child.getBoundingClientRect().height;
    }

    this.maxHeight = totalHeight + 'px';
  }
}
