import { Component, OnInit } from '@angular/core';
import { Basket } from 'src/app/services/basket/basket.types';
import { numberToPriceString } from '../../../../src/utils/numberUtils';
import {
  AuthenticationService,
  BasketService,
  PermissionGroup,
  SparesPermissions,
  Toast,
  ToastService
} from 'src/app/services';
import { OrderService } from 'src/app/services/order/order.service';
import { take } from 'rxjs/operators';
import { Router } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
import { UserService } from 'src/app/services/user-account/user.service';

@Component({
  selector: 'app-order-basket',
  templateUrl: './order-basket.component.html',
  styleUrls: ['./order-basket.component.scss']
})
export class OrderBasketComponent implements OnInit {
  public basket: Basket;
  public numberToPriceString = numberToPriceString;
  public isPlacingOrder = false;
  public errorMessage: string;
  public canPlaceOrder: boolean;
  public canViewOrderHistory: boolean;
  public isGettingBasket = false;

  constructor(
    private basketService: BasketService,
    private authenticationService: AuthenticationService,
    private toastService: ToastService,
    private orderService: OrderService,
    private router: Router,
    private userService: UserService
  ) {}

  public ngOnInit() {
    this.isGettingBasket = true;
    this.basketService.basket$.subscribe((basket: Basket) => {
      this.isGettingBasket = false;
      if (basket && basket.items) {
        this.basket = basket;
      }
    });
    this.setPermissionBooleans();
  }

  private setPermissionBooleans() {
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.Spares]: [SparesPermissions.CanPlaceOrder]
      })
      .subscribe(hasPermission => (this.canPlaceOrder = hasPermission));

    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.Spares]: [SparesPermissions.CanViewOrderHistory]
      })
      .subscribe(hasPermission => (this.canViewOrderHistory = hasPermission));
  }

  public placeOrder() {
    if (this.isPlacingOrder) {
      return;
    }
    this.isPlacingOrder = true;
    this.errorMessage = '';

    this.orderService
      .requestOrder(this.basket, this.userService.primaryOrganisationId)
      .subscribe(
        _ => {
          this.isPlacingOrder = false;
          this.toastService.showToast(
            new Toast('Request details emailed to purchase team')
          );
          this.basketService
            .getBasket()
            .pipe(take(1))
            .subscribe(basket => {
              this.basketService.basket$.next(new Basket(basket));
            });
          this.router.navigate(['spares/stock-list']);
        },
        (errorResponse: HttpErrorResponse) => {
          this.isPlacingOrder = false;
          const errorMessage = errorResponse.error;
          this.toastService.showErrorToast(errorMessage);
          return;
        },
        () => {
          this.isPlacingOrder = false;
          this.errorMessage = `Sorry something went wrong... please try again later`;
        }
      );
  }

  public handlesPlaceOrderClick() {
    if (this.isPlacingOrder) {
      return;
    }
    this.placeOrder();
  }
}
