import { Injectable } from '@angular/core';
import { Basket } from '../basket/basket.types';
import { Observable } from 'rxjs';
import { IOrder } from './order.types';
import { environment } from 'src/environments/environment';
import { AuthenticationService } from '../authentication/authentication.service';
import { HttpClient } from '@angular/common/http';
import { UserProfile } from '../user-account/user';

@Injectable({
  providedIn: 'root'
})
export class OrderService {
  private orderUrl = `${environment.apiBaseUrl}/purchase-request`;
  constructor(
    private authenticationService: AuthenticationService,
    private httpClient: HttpClient
  ) {}

  private get currentUserProfile(): UserProfile {
    return this.authenticationService.currentUserProfile$.getValue();
  }
  public requestOrder(
    basket: Basket,
    organisationId: string
  ): Observable<IOrder> {
    const items = basket.items.map(item => ({
      skuId: item.skuId,
      skuDescription: item.skuDescription,
      requestedByUserId: item.createdByUserId,
      requestedByUserName: item.createdByUserName,
      uom: item.uom,
      qty: item.qty
    }));

    let orderLink: string = this.orderUrl.concat(
      '?organisationUnitId=',
      organisationId
    );
    const payload = {
      items,
      siteId: this.currentUserProfile.primarySite.id,
      approvedByUserId: this.currentUserProfile.userId,
      approvedByUserName: this.currentUserProfile.name,
      approvedByEmailId: this.currentUserProfile.emailAddress
    };
    return this.httpClient.post<IOrder>(orderLink, payload);
  }
}
