import { Component, Input, OnInit } from '@angular/core';
import {
  ISkuStock,
  BasketService,
  ToastService,
  Toast,
  AuthenticationService,
  UserProfile
} from 'src/app/services';
import {
  safelyUnsubscribeFrom,
  AutoUnsubscribe
} from '../../../../utils/observableUtils';
import { Subscription } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { IBasketItem } from 'src/app/services/basket/basket.types';

AutoUnsubscribe;
@Component({
  selector: 'app-sku-stock',
  templateUrl: './sku-stock.component.html',
  styleUrls: ['./sku-stock.component.scss']
})
export class SkuStockComponent implements OnInit {
  @Input()
  public skuStock: ISkuStock;
  public visuallyExpanded = false;
  public detailsModalVisible = false;
  public isAddingToBasket = false;
  public qty = 1;
  public isQtyInOrderMultiples = true;
  public canAddToBasket: boolean = true;
  public addToBasketSubscription: Subscription;
  public userProfile: UserProfile;

  constructor(
    private basketService: BasketService,
    private toastService: ToastService,
    public authenticationService: AuthenticationService
  ) {
    this.userProfile = this.authenticationService.currentUserProfile$.getValue();
  }

  public ngOnInit() {
    if (this.skuStock.quantities.minimumOrderQuantity) {
      this.qty = this.skuStock.quantities.minimumOrderQuantity;
    }
  }

  public handleOrderMoreClick() {
    this.detailsModalVisible = true;
  }

  public handleDetailsModalClose() {
    this.detailsModalVisible = false;
  }

  public handleAddToBasket(): void {
    this.isAddingToBasket = true;
    safelyUnsubscribeFrom(this.addToBasketSubscription);
    let basketItem: IBasketItem;

    basketItem = {
      createdByUserId: this.userProfile.userId,
      createdByUserName: this.userProfile.name,
      skuId: this.skuStock.id,
      skuDescription: this.skuStock.description,
      uom: this.skuStock.uom,
      qty: this.qty
    };

    this.addToBasketSubscription = this.basketService
      .addToBasket(basketItem, this.userProfile.primarySite.id)
      .subscribe(
        () => {
          this.isAddingToBasket = false;
          this.detailsModalVisible = false;
          this.toastService.showToast(new Toast('Added to basket'));
        },
        (errorResponse: HttpErrorResponse) => {
          this.isAddingToBasket = false;
          const errorMessage = errorResponse.error;
          this.toastService.showErrorToast(errorMessage);
          return;
        }
      );
  }

  public handleQtyClipboard(event: ClipboardEvent) {
    event.preventDefault();
    return;
  }

  public disableRightClick(e) {
    e.preventDefault();
  }

  public handleQtyInputKeyDown(event) {
    if (event.key === 'Backspace' || event.key === 'Delete') {
      return;
    }
    if (isNaN(event.key) || event.keyCode === 32) {
      event.preventDefault();
    }
  }

  public handleQtyKeyup(event: KeyboardEvent) {
    if (
      event.key === 'Enter' &&
      this.qty > 0 &&
      this.qty.toString().length > 0 &&
      this.qty >= this.skuStock.quantities.minimumOrderQuantity
    ) {
      event.preventDefault();
      this.handleAddToBasket();
    }
  }
}
