import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PermissionGuard } from '../guards/permission.guard';
import {
  IRequiredPermissions,
  PermissionGroup,
  SparesPermissions
} from '../services';
import { LandingPageComponent } from '../shared-module/landing-page/landing-page.component';
import { StockListComponent } from './stock-list/stock-list.component';
import { OrderBasketComponent } from './order-basket/order-basket.component';
import { OrderHistoryComponent } from './order-history/order-history.component';

export const stockListRequiredPermissions: IRequiredPermissions = {
  [PermissionGroup.Spares]: [SparesPermissions.CanViewStockList]
};

export const basketRequiredPermissions: IRequiredPermissions = {
  [PermissionGroup.Spares]: [SparesPermissions.CanAddToBasket]
};

export const orderViewRequiredPermissions: IRequiredPermissions = {
  [PermissionGroup.Spares]: [SparesPermissions.CanViewOrderHistory]
};

export const routes: Routes = [
  {
    path: 'stock-list',
    component: StockListComponent,
    canActivate: [PermissionGuard],
    data: {
      permissionsRequired: stockListRequiredPermissions
    }
  },
  {
    path: '',
    component: LandingPageComponent
  },
  {
    path: 'order-basket',
    component: OrderBasketComponent,
    canActivate: [PermissionGuard],
    data: {
      permissionsRequired: basketRequiredPermissions
    }
  },
  {
    path: 'order-history',
    component: OrderHistoryComponent,
    canActivate: [PermissionGuard],
    data: {
      permissionsRequired: orderViewRequiredPermissions
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SparesRoutingModule {}
